import React from 'react'

function HowToPlay() {
  return (
    <div className="container">
        <h2>
        How to Play in the Lottery:</h2><p>
        <ol><li><strong>
        Access the DApp: </strong>To get started, you'll need access to a web browser with a compatible Ethereum wallet extension (like MetaMask).
        </li><li><strong>
        Install MetaMask: </strong> If you don't have MetaMask installed, visit the MetaMask website and install the extension for your browser. Follow the instructions to set up your wallet and fund it with some Ether (ETH).
        </li><li><strong>
        Access the Lottery DApp: </strong>Once your wallet is set up and funded, go to the website of our Lottery DApp.
        </li><li><strong>
        Connect Your Wallet: </strong>Click on the "Connect Wallet" button on the DApp interface. MetaMask will prompt you to connect your wallet. Allow the connection.
        </li><li><strong>
        Check Lottery Details: </strong>You'll see information about the current lottery, including the entry fee (0.001 ETH), the number of participants, and the remaining time to enter.
        </li><li><strong>
        Enter the Lottery: </strong>If you're interested in participating, click the "Enter Lottery" button. MetaMask will prompt you to confirm the transaction. Review the transaction details and confirm the payment.
        </li><li><strong>
        Wait for the Draw: </strong>Once the entry period ends, the manager will perform a random draw to select the winner. Keep an eye on the DApp for updates.
        </li><li><strong>
        Claim Your Reward: </strong>If you're the lucky winner, a "Claim Reward" button will appear next to your name. Click on it, and MetaMask will prompt you to confirm the claim transaction. Once confirmed, the prize will be transferred to your wallet.
        </li><li><strong>
        Celebrate Your Win: </strong>Congratulations! You've successfully participated in the lottery and claimed your reward. Enjoy your winnings!
        </li></ol>
        Remember that participating in the lottery involves a level of risk, and winning is based on chance. Only invest what you can afford to lose, and always make sure to interact with secure and trusted DApps.
        <br/><em><marquee>Enjoy the excitement of the Lottery DApp and may luck be on your side!</marquee></em>
        </p>


    </div>
  )
}

export default HowToPlay