import React, {useState, useEffect} from "react";
import constants from './constants';
const ethers=require("ethers");
function Home () {
  
    const [currentAccount, setCurrentAccount] = useState("");
    const [contractInstance, setContractInstance] = useState("");
    const [status, setStatus] = useState(false);
    const [isWinner, setIsWinner] = useState("");

    useEffect(() => {
        const loadBlockchainData = async () => {
            if (typeof window.ethereum !== 'undefined') {
                const provider = new ethers.providers.Web3Provider(window.ethereum);
                try {
                    const signer = provider.getSigner();
                    const address = await signer.getAddress();
                    setCurrentAccount(address);
                    window.ethereum.on('accountsChanged', (accounts) => {
                        setCurrentAccount(accounts[0]);
                        console.log(currentAccount);
                    })
                } catch (err) {
                    console.error(err);
                }
            } else {
                alert('Please install Metamask to use this application')

            }
        };

        const contract = async () => {
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contractIns = new ethers.Contract(constants.contractAddress, constants.contractABI, signer);
            setContractInstance(contractIns);
            try{
              const status = await contractIns.isComplete();
              setStatus(status);
              const winner = await contractIns.getWinner();
              if (winner === currentAccount) {
                  setIsWinner(true);
              } else {
                  setIsWinner(false);
              }
            }
            catch (error) {
              console.log(signer);
              console.error('Error:', error);
            }
        }

        loadBlockchainData();
        contract();
    }
    , [currentAccount]);

    const enterLottery = async () => {
        const amountToSend = ethers.utils.parseEther('0.01');
        const tx = await contractInstance.enter({value: amountToSend,});
        await tx.wait();
    }

    const claimPrize = async () => {
        const tx = await contractInstance.claimPrize();
        await tx.wait();
    }
    


    return(
        <div className="container">
            <h1>Lottery Page</h1>
            <h2>
            Introduction to Lottery DApp:</h2>
            <p>
            Welcome to the Lottery DApp! Our DApp is a decentralized lottery system that allows you to participate in a fun and exciting lottery game using Ethereum. Here's how it works: The lottery is managed by a designated manager. Players can enter the lottery by paying a small entry fee of 0.001 ETH. Once the entry period is over, the manager will randomly select a winner from the pool of participants. The lucky winner will be able to claim the prize by clicking on the "Claim Reward" button. It's a fair and transparent way to test your luck and potentially win big!
            </p>
            <div className="button-container">
                {status ? ( isWinner ? (<button className="enter-button" onClick={claimPrize}> Claim Prize </button>): 
                (<p>Better Luck! Next Time</p>)) : 
                (<button className="enter-button" onClick={enterLottery}> Enter Lottery </button>)
                }
            </div>
        </div>
    ) 

}

export default Home;