import React,{useState,useEffect} from 'react';
import Home from './Home';
import PickWinner from './PickWinner';
import PreviousLottery from './PreviousLottery';
import HowToPlay from './HowToPlay';
import {BrowserRouter,Routes,Route,Link}from 'react-router-dom';
import './App.css';
import main from './main.png';
import {ethers} from 'ethers';

function App() {
  const [currentAccount, setCurrentAccount] = useState("");
    useEffect(() => {
        const loadBlockchainData = async () => {
            if (typeof window.ethereum !== 'undefined') {
                const provider = new ethers.providers.Web3Provider(window.ethereum);
                try {
                    const signer = provider.getSigner();
                    const address = await signer.getAddress();
                    setCurrentAccount(address);
                    window.ethereum.on('accountsChanged', (accounts) => {
                        setCurrentAccount(accounts[0]);
                    })
                } catch (err) {
                    console.error(err);
                }
            } else {
                alert('Please install Metamask to use this application')

            }
        };
        loadBlockchainData();
    }
    , [currentAccount]);
  return (
    <div>
        <BrowserRouter>
            <nav className="navbar navbar-expand-lg " data-bs-theme="dark" style={{backgroundColor:"#0d0d0d"}}>
                <Link className="navbar-brand mx-3" to="/">
                    <img src={main} alt="Logo" width="60" height="40" className="d-inline-block align-text-top"/>
                </Link>
                <Link className="navbar-brand " to="/">Home</Link>
                
                <Link className="navbar-brand mx-3" to="/PickWinner">PickWinner</Link>
                <Link className="navbar-brand mx-3" to="/PreviousLottery">PreviousLotteries</Link>
                <Link className="navbar-brand mx-3" to="/HowToPlay">HowToPlay</Link>
                  <button className="btn btn-outline-success my-9" type="submit" style={{ marginLeft: "auto" ,marginRight:"4px"}}>{currentAccount}</button>
                </nav>

            <Routes>
                <Route path="/PreviousLottery" element={<PreviousLottery />}></Route>
                <Route path="/PickWinner" element={<PickWinner />}></Route>
                <Route path="/HowToPlay" element={<HowToPlay />}></Route>
                <Route path="/" element={<Home />}></Route>
            </Routes>
            
        </BrowserRouter>


    </div>
  )
}

export default App;